.greeting-card {
    width: 300px;
    height: 200px;
    background-color: #f0f8ff; /* Light blue background color */
    border: 2px solid #ccc;
    border-radius: 10px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px auto; /* Center the card */
    text-align: center;
    font-family: Arial, sans-serif;
    font-size: 24px;
}